import keyMirror from 'keymirror';

const Modes = keyMirror({
    BRUSH: null,
    ERASER: null,
    LINE: null,
    FILL: null,
    SELECT: null,
    RESHAPE: null,
    PEN: null,
    OVAL: null,
    RECT: null,
    ROUNDED_RECT: null,
    TEXT: null
});

export default Modes;



